*{

  margin: 0;
  box-sizing: border-box;
  padding: 0;
}


.navbar{
  width: 100%;
  height: 10vh;
  background: linear-gradient(30deg, rgba(22,22,22,0.8),rgba(222,22,22,0.7));
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
a{
  color: #fff;
  text-decoration: none;
}


.footer{
  width: 100%;
  height: 10vh;
  background: linear-gradient(30deg, rgba(22,22,22,0.8),rgba(222,22,22,0.7));
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;


}
.book-list{
  min-height: 80vh;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  justify-items: center;
  text-align: center;

}
.favorites{
  min-height: 80vh;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  justify-items: center;
  text-align: center;

}
img{
  width: 300px;
  height: 300px;
  transition: all 0.3s ease;
  border-radius: 1rem;
  cursor:  pointer;
}
img:hover{
  transform: scale(1.1);
}
.book{
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
  /* border: 1px solid #000; */
  border-radius: 1rem;
  padding: 0 1rem;
  box-shadow: 2px 2px 2px 2px rgba(222,222,222,0.8);


}
.btn{
  border: none;
  background: #fff;
  color: #000;
  border-radius: 1rem;
  padding: 6px 10px;
}
.btn:hover{
  background: rgba(225, 189, 189, 0.8);
  color: #fff;
  transition: all 0.3s ease;
  transform: scale(1.1);
}
.fav{
  display: grid;
  grid-auto-columns: max-content;
  margin: 10rem 18rem;
}
button{
  padding: 10px;
  color: #fff;
  background: #000;
  width: 300px;
  font-size: 15px;
  border-radius: 5px;
  margin: 1rem 0;
  cursor: pointer;
  
}
button:hover{
  background: #fff;
  color: #000;
   transition: 0.3s ease-in-out;
}
.book-details{
  min-height: 80vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  /* text-align: center; */
  padding: 0 2rem;

}
.box{
  
  border-radius: 1rem;
  box-shadow: 2px 2px 2px 2px rgba(222,222,222,0.8);
  padding: 0 1rem;
}
.box2{

  box-shadow: 2px 2px 2px 2px rgba(222,222,222,0.8);
  border-radius: 1rem;
  margin: 0 1rem;
}
.title{
  margin: 1rem 0;
  text-align: center;
 box-shadow: 2px 2px 2px 2px rgba(238, 227, 227, 0.8);
 border-radius: 1rem;

 
}
.description{padding: 0 1rem; font-size: 1.4rem;}
.para{
  padding: 0 1rem 0.5rem ;
  font-size: 0.8rem;

}


/* responsive */


@media screen and (max-width: 980px){
 .book-list{
 
   
  grid-template-columns: repeat(2,1fr);
  
 
 }

}
@media screen and (max-width: 767px){
 .book-list{
  grid-template-columns: repeat(2,1fr);
  
 }

}

 @media screen and (max-width: 460px){
  .book-list{
    display: block;

  }
  h1{
    font-size: 1.3rem;
  }
  .btn{
    padding: 6px 2px;
    font-size: 1rem;
  }
  .book-details{
    display: block;
    margin: 1rem 0;
  }
  .box{
    margin: 1rem 28px 3rem;
  }
  .box2{
    box-shadow: 2px -12px 2px 2px rgba(222,222,222,0.8);
  }
  .book{
    margin: 1rem 4rem;
  }
  .fav{
    margin: 10rem 2rem
  }

} 

